.persons {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;
}

.persons__item {
  flex-basis: 31%; //270/870*100
  margin-right: 3.4%; //30/870*100

  &:last-child {
    margin-right: 0;
  }
}

.persons__img {
  display: block;
  width: 100%;
}

.persons__name {
  font-size: 13px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.231;
  font-family: 'roboto_condensedbold', 'Arial', sans-serif;
  margin: 5.5% 0 0 0;

  @media (min-width: 1500px) {  //18/16*100
    @include dynamicText(81.25%, 26, 13, 1500px, 3000, 1500);
  }
}
.persons__post {
  font-size: 12px;
  color: $colorText;
  text-transform: uppercase;
  line-height: 1.333;
  margin: 0 0 5.5% 0;

  @media (min-width: 1500px) {  //18/16*100
    @include dynamicText(75%, 24, 12, 1500px, 3000, 1500);
  }
}

.persons__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.persons__cv {
  font-size: 14px;
  color: $red;
  text-transform: uppercase;
  line-height: 1.143;
  text-decoration: none;
  padding-right: 25px;
  background: url("/img/arrow-left.svg") right center no-repeat;

  @media (min-width: 1500px) {  //18/16*100
    @include dynamicText(87.5%, 28, 14, 1500px, 3000, 1500);
  }

  &:hover {
    span {
      border-color: $red;
    }
  }

  span {
    border-bottom: 1px solid transparent;
    transition-duration: 500ms;
    transition-property: border-color;
  }
}

@media (max-width: 980px) {
  .persons {
    flex-wrap: wrap;
  }
  .persons__item {
    flex-basis: 47%;
    margin-right: 3%;
    margin-top: 3.5%;
    margin-bottom: 3.5%;
  }
}

@media (max-width: 560px) {
  .persons {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .persons__item {
    flex-basis: auto;
    margin-right: 0;

    &:nth-child(even) {
      margin-right: 0;
    }
  }
}